import React from 'react'
import './Skills.css'
import { FaHtml5, FaCss3, FaReact, FaBootstrap, FaNpm, FaPython} from "react-icons/fa";
import { SiJavascript, SiFlask } from "react-icons/si";

class Skills extends React.Component{
	render(){
		const icon = {
			size : '5rem',
			color : '#6e6e6e'
		}
		return(
			<div className='skills'>
				<h1> My Tools! </h1>
				<hr />
				<div className='skill-container'>
					<div className='skill-set'>

						<div className='skill-div'>
							<FaHtml5 size={icon.size} color={icon.color}/>
							<p> HTML5 </p>
						</div>
						<div className='skill-div'>
							<FaCss3 size={icon.size} color={icon.color} />
							<p> CSS3 </p>
						</div>
						<div className='skill-div'>
							<SiJavascript size={icon.size} color={icon.color} />
							<p> JavaScript </p>
						</div>
						<div className='skill-div'>
							<FaPython size={icon.size} color={icon.color} />
							<p> Python </p>
						</div>
						<div className='skill-div'>
							<FaReact size={icon.size} color={icon.color} />
							<p> React JS </p>
						</div>
						<div className='skill-div'>
							<SiFlask size={icon.size} color={icon.color} />
							<p> Flask </p>
						</div>
						<div className='skill-div'>
							<FaBootstrap size={icon.size} color={icon.color} />
							<p> Bootstrap </p>
						</div>
						<div className='skill-div'>
							<FaNpm size={icon.size} color={icon.color} />
							<p> NPM </p>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Skills;