import React from 'react'
import './NewNavigation.css'
import { Navbar, Nav } from 'react-bootstrap';
import { NavLink } from 'react-router-dom';

class NewNavigation extends React.Component{
	
	render(){
		const s = {
			color: 'white'
		};
		return (
			<>
				<Navbar fixed='top' collapseOnSelect expand="lg" bg="primary" variant="dark">
				  <Navbar.Brand href="/">SA</Navbar.Brand>
				  <Navbar.Toggle aria-controls="responsive-navbar-nav" />
				  <Navbar.Collapse id="responsive-navbar-nav ">
				    <Nav className="ml-auto">
				    	<NavLink className="nav-link" to="/my_portfolio" exact activeStyle={s}>Home</NavLink>
				    	<NavLink className="nav-link" to="/my_portfolio/about" exact activeStyle={s} >About</NavLink>
				    	<NavLink className="nav-link" to="/my_portfolio/projects" exact activeStyle={s} >Projects</NavLink>
				    	<NavLink className="nav-link" to="/my_portfolio/skills" exact activeStyle={s} >Skills</NavLink>
				    	<NavLink className="nav-link" to="/my_portfolio/interests" exact activeStyle={s} >Interests</NavLink>
				    	<NavLink className="nav-link" to="/my_portfolio/blogs" exact activeStyle={s} >Blogs</NavLink>
				    	<NavLink  className="nav-link" to="/my_portfolio/contact" exact activeStyle={s} >Contact</NavLink>	  	
				    </Nav>
				  </Navbar.Collapse>
				</Navbar>
			</>
		);
	}
}

export default NewNavigation