import React from 'react';
import './Project.css';
import { Card, CardGroup } from 'react-bootstrap';

class Project extends React.Component{
	render(){
		return(
			<>
				
				<Card className='project-card'>
				    <Card.Img variant="top" src={this.props.proj.img} />
			    	<Card.Body>
			      		<Card.Text>
					        {this.props.proj.desc}
			      		</Card.Text>
			    	</Card.Body>
			    	<Card.Footer className='icons'>
			      		<a className='grow' href={this.props.proj.github} target="_blank" rel="noopener noreferrer">
								<img src={process.env.PUBLIC_URL + '/icons/github.svg'} alt='github'/>
						</a>
						<a className='grow' href={this.props.proj.web} target="_blank" rel="noopener noreferrer">
								<img src={process.env.PUBLIC_URL + '/icons/internet.svg'} alt='web'/>
						</a>
			    	</Card.Footer>
				</Card>
				
				
			</>
		);
	}
}

export default Project;