import React from 'react';
import './App.css';
// import Navigation from '../components/Navigation'
import Home from '../components/Home'
import About from '../components/About'
// import Works from '../components/Works'
import Projects from '../components/Projects'
import Contact from '../components/Contact'
import NewNavigation from '../components/NewNavigation'
import Skills from '../components/Skills'
import Interests from '../components/Interests'
import Blogs from '../components/Blogs'

import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

function App() {
  return (
    <>
      <Router>
          <NewNavigation />
          <div className='container'>
              <Switch>
                <Route path="/my_portfolio" exact component={Home}/>
                <Route path="/my_portfolio/about" component={About}/>
                <Route path="/my_portfolio/projects" component={Projects} />
                <Route path="/my_portfolio/skills" component={Skills} />
                <Route path="/my_portfolio/interests" component={Interests} />
                <Route path="/my_portfolio/blogs" component={Blogs} />
                <Route path="/my_portfolio/contact" component={Contact}/>
              </Switch>
          </div>

      </Router>
    </>
    
  );
}

export default App;
