import React from 'react';
import Project from './Project'
// import 'font-awesome';
import { CardDeck } from 'react-bootstrap';


class Projects extends React.Component{

	render(){
		const projs = [
		{
			name: 'tabQR',
			img: 'https://user-images.githubusercontent.com/8282374/94768304-b16f3880-03cc-11eb-933a-663349322608.png',
			desc: [<strong>TabQR</strong>," is a Firefox extension, which generates a QR code of current active tab. Which can be further scanned using any smart phone and that active tab will be opened in the smart phone's browser. "],
			github: 'https://github.com/agrawalsourabh/TabQR-FirefoxExt',
			web:  'https://addons.mozilla.org/en-US/firefox/addon/tabqr/'
		},
		{
			name: 'Shortest Path Finder',
			img: 'https://user-images.githubusercontent.com/8282374/79354109-99f4de00-7f59-11ea-8760-9e218496e587.png',
			desc: [<strong>Shortest Path Finding Visualiser</strong>, " is a web based application which visualises ", <strong> Dijkstra's </strong>, " and ", <strong>Astar's</strong>, " shortest path finding algorithm. And deployed to ", <strong>Heroku</strong> , " server."],
			github: 'https://github.com/agrawalsourabh/ShortestPathFinder' ,
			web:  'https://shortest-path-finder.herokuapp.com/'
		},
		{
			name: 'Recipe App',
			img: 'https://user-images.githubusercontent.com/8282374/94892909-93c0d280-04a3-11eb-9d1e-dbc31a23f5e2.png',
			desc: [<strong>Recipe App</strong>, " is a web based application which searches the dish recipe."],
			github: 'https://github.com/agrawalsourabh/RecipeApp' ,
			web:  'https://agrawalsourabh.github.io/RecipeApp/'
		}

	];
		return(
			<div className='work'>
				<h1> Work </h1>
				<hr />
				<CardDeck>
					<Project className='proj-card' proj= {projs[0]} />
					<Project proj= {projs[1]}/>
					<Project proj= {projs[2]}/>
				</CardDeck>
			</div>
		);
	}
}

 
export default Projects;