import React from 'react'
import './Contact.css'
import { FaTelegramPlane, FaGithub, FaMedium, FaTwitter, FaLinkedin, FaRegCopy} from "react-icons/fa";
import { SiGmail } from "react-icons/si";

class Contact extends React.Component{
	render(){
		const icon = {
			size : '3rem',
			color : '#6e6e6e'
		}

		return(
			<div className='contact'>
				<h1> Contact Me </h1>
				<hr />

				<div className='contact-div'>
					<div className='contact-img'>
						<img src={process.env.PUBLIC_URL + '/img/contact.svg'} alt='contact' />
					</div>
					<div className='contact-detail'>
						<div className='contact-med'>
							<SiGmail size={icon.size} color={icon.color}/>
							<div className='con-links'>
								<p> agrawal.sourabh644@gmail.com </p>
							</div>
						</div>
						<div className='contact-med'>
							<FaGithub size={icon.size} color={icon.color}/>
							<div className='con-links'>
								<a href='https://github.com/agrawalsourabh' target="_blank" rel="noopener noreferrer">agrawalsourabh</a>
								
							</div>
						</div>
						<div className='contact-med'>
							<FaMedium size={icon.size} color={icon.color}/>
							<div className='con-links'>
								<a href='https://medium.com/@agrawal.sourabh644'target="_blank" rel="noopener noreferrer"> @agrawal.sourabh644 </a>
								
							</div>
						</div>
						<div className='contact-med'>
							<FaLinkedin size={icon.size} color={icon.color}/>
							<div className='con-links'>
								<a href='www.linkedin.com/in/sourabh-agrawal-1556b379' target="_blank" rel="noopener noreferrer"> linkedin.com/in/sourabh-agrawal-1556b379 </a>
								
							</div>
						</div>
						<div className='contact-med'>
							<FaTwitter size={icon.size} color={icon.color}/>
							<div className='con-links'>
								<a href='https://twitter.com/Sourabh40889334' target="_blank" rel="noopener noreferrer"> @Sourabh40889334 </a>
								
							</div>
						</div>
						
					</div>
				</div>
			</div>
		);
	}
}

export default Contact