import React from 'react'
import './About.css'

class About extends React.Component{
	render(){
		return(
			<div className='about'>
				<h1> About Me </h1>
				<hr />
				<div className='about-sec'>
					<img src={process.env.PUBLIC_URL + '/img/web-developer.svg'} class="db" alt="web-developer" />

					<div className='about-txt'>
						<p> I am <strong> passionate </strong> about creating <strong> web experiences </strong>
							that improve the <strong> lives </strong> of those around me.
						</p>
						<p>
							I focus on <strong> crafting </strong> clean and <strong> user-friendly </strong> expericences.
						</p>
					</div>
				</div>
			</div>
		);
	}
}

export default About;