import React from 'react'
import './Blogs.css'
import Blog  from './Blog'

class Blogs extends React.Component{
	render(){

		const blogs = [
			{
				title: "How Flexbox makes life easy?!",
				img:  "https://miro.medium.com/max/576/1*CEE_wiZjmlhnK0wiSLRfeA.png",
				link:  "https://medium.com/@agrawal.sourabh644/how-flexbox-makes-life-easy-d1236f3512b0"
			},
			{
				title: "Build your first Flask web app!!",
				img:  "https://miro.medium.com/max/814/1*GVvohkgdf-42P6R9CwEYBw.png",
				link:  "https://medium.com/@agrawal.sourabh644/build-your-first-flask-web-app-72fbadcf7422"
			}
		];

		return(
			<div className='blogs'>
				<h1> My Blogs </h1>
				<hr />
				<div className = 'blog-list'>
					<Blog b={blogs[0]}/>
					<Blog b={blogs[1]}/>
				</div>
			</div>
		);
	}
}

export default Blogs;