import React from 'react'
import './Home.css'

class Home extends React.Component {
	render(){
		return (
			<div className='introduction'>
				<div className="image_div">
					<img src={process.env.PUBLIC_URL + '/img/avatar.svg'} class="db" alt="Sourabh Agrawal" />
				</div>

				<div className='introduction-text'>
					<p class="f6 gray mv1 greeting"> Hi! I am </p>
					<p class="f3 link dark-blue name" >Sourabh Agrawal</p>
					<p class="f6 gray mv1 intro">I design things for the web.</p>
				</div>
				
			</div>
		);
	}
}

export default Home